.TodoItem {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 15px;
    color: #FAFAFA;
  }
  .low {
    background-color: #61DAFA;
  }
  .normal {
    background-color: #006C67;
  }
  .important {
    background-color: #003844;
  }
  .urgent {
    background-color: #FFB100;
  }
  .low > .Icon-check--active {
    color: #61DAFA;
  }
  .normal > .Icon-check--active {
    color: #006C67;
  }
  .important > .Icon-check--active {
    color: #003844;
  }
  .urgent > .Icon-check--active {
    color: #FFB100; 
  }
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    color: black;
    /* background-color: #CCC; */
  }
  
  .Icon-check {
    position: absolute;
    left: 12px;
    background-color: #F7F9F9;
    border-radius: 20px;
  }
  
  
  .Icon-delete {
    position: absolute;
    top: -24px;
    right: 0;
  }
  .Icon-delete:hover {
    color: red;
  }